<template>
  <v-card>
    <v-card-title class="justify-center" style="background-color:#E69A8D">
      <div style="text-align: center; color:white; font-weight:400"> 나의 대출 한도 상담 </div>
    </v-card-title>
    <v-card-text style="margin-bottom:0px; padding-bottom:0px">
      <v-form ref="form">
        <v-text-field v-model="limit.name" label="이름" color="#5F4B8B" prepend-icon="mdi-account" type="text"
          autocomplete="off" :rules="[rules.required]"></v-text-field>
        <v-text-field v-model="limit.phoneNumber" label="연락처" color="#5F4B8B" prepend-icon="mdi-phone"
          type="text" autocomplete="off" :rules="[rules.required, rules.phoneNumber.pattern]"></v-text-field>

        개인(신용)정보/수집/이용 제공동의서

        <v-row justify="center" align="center">
          <v-col xl="6" align="center">
            <v-btn dense text small color="#fd5c63" @click="clickTemrs()"> 이용약관 </v-btn>
          </v-col>

          <v-col xl="6" align="center">
            <v-btn dense text small color="#fd5c63" @click="clickPrivacy()"> 개인정보수집 </v-btn>
          </v-col>
        </v-row>
        <v-checkbox color="#5F4B8B" v-model="isAgreed" class="checkbox" dense label="개인정보이용/전화권유 동의"> </v-checkbox>
      </v-form>
    </v-card-text>

    <v-card-actions>
      <v-btn block @click="limitCheck" color="#fd5c63"> 신청 </v-btn>
    </v-card-actions>
    
    <terms :visible.sync="privacy.visible" :content="privacy.content" :title="privacy.title" />
    <terms :visible.sync="terms.visible" :content="terms.content" :title="terms.title" />

  </v-card>
</template>

<script>
import Terms from './Terms.vue';
export default {
  created() {
    this.getTermsText()
    this.getPrivacyText()
  },
  components: { Terms },

  methods: {
    clickTemrs() {
      this.terms.visible = true;
      this.terms.confirm = true;
    },
    clickPrivacy() {
      this.privacy.visible = true;
      this.privacy.confirm = true;
    },
    reset() {
      this.isAgreed = false;
      this.$refs.form.reset();
    },
    limitCheck() {

      this.limit.name = this.limit.name.trim()
      this.limit.phoneNumber = this.limit.phoneNumber.trim()
      
      if(!this.$refs.form.validate()) {
        return;
      }

      if(!(this.terms.confirm === true && this.privacy.confirm === true)) {
        this.$swal.fire({
          icon: 'warning',
          title: '요청 실패',
          text: '약관을 확인해 주시기 바랍니다',
        })
        return;
      }
      if(this.isAgreed === false) {
        this.$swal.fire({
          icon: 'warning',
          title: '요청 실패',
          text: '약관에 동의하여 주시기 바랍니다',
        })
        return;
      }

      this.$swal.fire({
        icon: 'success',
        title: '요청 성공',
        text: '접수되었습니다',
      })
      this.reset();
    },
    getTermsText() {
      this.$axios.get("/text/terms").then((res) => {
        this.terms.content = res.data
        // console.log(this.terms.content)
      }).catch((err) => {
        alert(err)
      })  
    },
    getPrivacyText() {
      this.$axios.get("/text/privacy").then((res) => {
        this.privacy.content = res.data
        // console.log(this.terms.content)
      }).catch((err) => {
        alert(err)
      })  
    }
    
  },
  data() {
    return {
      isAgreed: false,
      limit: {
        name: "",
        phoneNumber: ""
      },
      rules: {

        required: value => !!value || '필수 입력 항목입니다.',
        phoneNumber: {
          pattern: value => {
            return (/^[0-9]{10,11}$/.test(value)) || '10~11 자리 숫자만 입력 가능합니다.'
          }
        }
      },
      
      privacy: {
        visible: false,
        title: "개인정보 수집/이용/정보제공 동의서",
        content: "",
        confirm: false
      },
      terms: {
        visible: false,
        title: "이용 약관",
        content: "",
        confirm: false
      }
    }
  }
}
</script>

<style scoped>
::v-deep .checkbox .v-label {
 font-size: 10px;
}

</style>

